/* Fonts */

@font-face {
  font-family: 'inter';
  src: url('/fonts/InterRegular.ttf');
}

@font-face {
  font-family: 'inter_bold';
  src: url('/fonts/InterBold.ttf');
}

/* Navbar */

.bar {
    width: 100%;
    top: 0;
    position: fixed;
    height: 20vh;
    background-color: rgba(255, 255, 255, 0.3);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    overflow-y: none;
    z-index: 10;
}

h1 {
    font-family: "inter_bold";
    font-weight: bold;
    font-size: 7.5vh;
    color: white;
    /* text-shadow: 2px 2px 10px #000000; */
    padding: 0;
    margin: 0;
    /* width: 10vh; */
    height: 100%;
    /* text-align: center; */
    user-select: none;
    /* vertical-align: middle; */
    line-height: 8.5vh;
}

.blackTile {
    background-color: black;
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;
}

.tile {
    width: 10vh;
    height: 10vh;
    border-radius: 2vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbarItems {
    color: black;
    font-size: 4vh;
}

img {
    width: 100%;
    margin-top: 20vh;
    margin-bottom: 40vh;
}